.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
  }
  
  .navbar__black {
    background-color: #111;
  }
  
  .logo {
    position: fixed;
    left: 20px;
    width: 100px;
    object-fit: contain;
  }

  .navbar__items {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
    padding-right: 70px;
    margin-left: auto; /* Aligns the items to the right */
}

.navbar__item {
    color: white;
    margin: 0 20px;
    cursor: pointer;
    transition: color 0.3s;
}

.navbar__item:hover {
    color: #e50914;
}
  
  .avatar {
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
  }
  @media (max-width: 600px) {
    .navbar__items {
      display: none; /* Hide the navbar items on small screens */
    }
  }